import Model from "./model";

class Example extends Model {

    static icon = {
        name: 'test',
        type: 'la'
    }

    attributes = {
        id: { prop: 'id', comment: "Identifiant" },
    }

    casts = {
        created: 'date',
        updated: 'date'
    }

    constructor(options = {}) {
        super(options);
        this.model = eval(this.__resolve.model(options.name || this.constructor.name))
    }
}

export default Example
