<template>
  <ssf-container not-full
                 id="preload"
                 class="position-fixed justify-content-center align-items-center bg-color-1"
                 :class="className">

    <ssf-container not-full
                   class="d-block w-100 text-center text-white fa-1x5 letter-container animated rubberBand infinite"
                   :style="`animation-delay: ${name.length / 3}s !important`">

      <ssf-container not-full
                     v-for="(letter, $idx) in name"
                     :key="key(letter, $idx)"
                     class="animated rotateInDownLeft faster d-inline-block h1"
                     :style="`animation-delay: ${$idx / 3}s !important`">
        {{ letter }}
      </ssf-container>

    </ssf-container>
  </ssf-container>
</template>

<script>

  import { key } from "app/vue/utils";

  let interval;

  export default {
    name: "PreLoad",

    components: {},

    data() {
      return {
        className: "d-flex",
        animate  : true,
        name     : 'Cloufit'
      }
    },

    created() {
      if (this.$store.getters.APP_ENV === 'production' || this.$store.getters.APP_ENV === 'pre-production')
        this.run()
      else
        this.close()
    },

    methods: {

      key: (letter, index) => key({ name: letter, id: index }),

      animateName() {
        return new Promise((resolve, reject) => {
          setTimeout(() => resolve(true), (this.name.length / 2) * 1000)
        })
      },

      async close() {
        await this.$store.dispatch('preloading', false);
        if (this.$store.getters.APP_ENV === 'production' || this.$store.getters.APP_ENV === 'pre-production')
          await this.animateName();
        this.className = "d-flex animated zoomOut";
        setTimeout(() => {
          this.className = "d-none";
          clearInterval(interval);
        }, 1000)
      },

      run() {
        let self = this, i = 0;
        interval = setInterval(() => {
          if ((document.readyState === "complete" || document.readyState === "ready") || i++ > 10) {
            if (this.$route.name !== 'home' || this.$store.getters.outfits) {
              self.close();
              clearInterval(interval);
              interval = null
            }
          }
        }, 1000)

      }
    }
  }
</script>

<style scoped lang="scss">

  #preload {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 16777271;
    background-color: #fff;

  }

</style>
