<template>
  <div class="loader simple-circle"
       :style="`{
       --reset:0;
    --loader-width: ${size};
    --loader-height: ${size};
    --loader-color-primary: ${parent.colorPrimary};
    --loader-color-secondary: ${parent.colorSecondary};
    --line-width: ${stroke};
    --animation-duration: ${duration};
    --loader-initial-scale: 0.1;
  }`">
  </div>
</template>

<script>
  export default {
    name: "simple-circle",

    computed: {
      parent() {
        return this.$parent.$props
      },

      size() {
        const size = this.parent.size;
        return typeof size === 'string' ? size : `${size}px`
      },

      stroke() {
        const stroke = this.parent.stroke;
        return typeof stroke === 'string' ? stroke : `${stroke}px`
      },

      duration() {
        const duration = this.parent.duration;
        return typeof duration === 'string' ? duration : `${duration}s`
      }

    }
  }
</script>

<style scoped lang="scss">

  .loader {
    &,
    &:before,
    &:after {
      content: '';
      box-sizing: border-box;
      -webkit-box-flex: 0;
      -ms-flex-positive: 0;
      flex-grow: 0;
      -ms-flex-negative: 0;
      flex-shrink: 0;
    }

    &.simple-circle {
      -webkit-transform-origin: center center;
      transform-origin: center center;
      border: var(--line-width, 4px) solid var(--loader-color-secondary, #fff);
      border-right-color: var(--loader-color-primary, #00f);
      width: var(--loader-width, 100px);
      height: var(--loader-height, 100px);
      border-radius: 50%;
      -webkit-animation: circle-loader var(--animation-duration, 1s) infinite ease-out;
      animation: circle-loader var(--animation-duration, 1s) infinite ease-out;
    }

    @-webkit-keyframes circle-loader {
      0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
      }
      100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
      }
    }

    @keyframes circle-loader {
      0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
      }
      100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
      }
    }
  }

</style>
