<template>
  <div class="card">

    <ssf-container name="card-header"
                   class="card-header"
                   :class="headerClass"
                   v-if="hasSlot('header')">
      <slot name="header"/>
    </ssf-container>

    <ssf-container name="card-body"
                   class="card-body"
                   :class="bodyClass"
                   v-if="hasSlot('body')">
      <slot name="body"/>
    </ssf-container>

    <ssf-container name="card-footer"
                   class="card-footer"
                   :class="footerClass"
                   v-if="hasSlot('footer')">
      <slot name="footer"/>
    </ssf-container>

  </div>
</template>

<script>
export default {
  name: 'ssf-card',

  props: {
    headerClass: { type: String | null, required: false, default: false },
    bodyClass  : { type: String | null, required: false, default: false },
    footerClass: { type: String | null, required: false, default: false },
  },

  methods: {
    hasSlot(name = 'default') {
      return !!this.$slots[name] || !!this.$scopedSlots[name];
    },
  }
}
</script>

