import helpers from "app/vue/utils/helpers";
import moment  from "moment";

const isObject = (val) => val instanceof Object;
const isMoment = (val) => val instanceof moment;

const parseDate = (data) => {
  Object.keys(data).map(key => {
    if (isObject(data[key]))
      parseDate(data[key])

    if (isMoment(data[key]))
      data[key] = data[key].format('YYYY-MM-DD HH:mm:ss')
  })
  return data
};

const __options = (options) => {
  if (options.data)
    options.data = parseDate(options.data)
  if (!options.headers)
    options.headers = {};
  // options.headers['X-API-KEY'] = process.env.API_KEY;
  options.headers['X-ACCESS-KEY'] = localStorage.getItem((require('app/vue/store').default).getters.cookieAccessKey);
  if (helpers.loading)
    options.loading = helpers.loading;

  return options;
};

const __createError = (error) => {
  let message = error && error.response && error.response.data && error.response.data.data && error.response.data.data.error
    ? error.response.data.data.error : error.message || "Une erreur est survenue, merci de réessayer plus tard.",
    status = error && error.response && error.response.status ? error.response.status : 500;
  return { message, status }
};

export {
  __options,
  __createError
}
