import Model               from "./model";
import { filter, shuffle } from "lodash";
import Season              from "app/models/season";

import Upload                from './upload'
import axios                 from "axios";

class Outfit extends Model {

  static icon = {
    name: 'test',
    type: 'la'
  }

  attributes = {
    id      : { prop: '_id', comment: "Identifiant" },
    season  : { prop: 'season', comment: "Saison" },
    seasonID: { prop: 'season_id', comment: "Saison" },
    image   : { prop: 'image', comment: "Photo de la tenue" },
    alt     : { prop: 'alt', comment: "Alt" },
    complete: { prop: 'complete', comment: "Tenue complète ?" },
  }

  casts = {
    season: (value) => (new Season()).__setItemAttributes(value)
  }

  constructor(options = {}) {
    super(options);
    this.model = eval(this.__resolve.model(options.name || this.constructor.name))
  }

  static src(path) {
    return `${process.env.PUBLIC_PATH}static/img/outfits/${path}`
  }

  static sort(outfits) {
    if (outfits) {
      const hot = require('app/vue/utils').hot(),
        summer = shuffle(filter(outfits, outfit => outfit.complete && outfit.season.id === Season.SUMMER)),
        winter = shuffle(filter(outfits, outfit => outfit.complete && outfit.season.id === Season.WINTER)),
        unknown = shuffle(filter(outfits, outfit => outfit.complete && outfit.season.id === Season.MIX)),
        noComplete = shuffle(filter(outfits, outfit => !(outfit.complete === true)));
      return filter(unknown.concat((hot ? summer : winter)).concat((hot ? winter : summer)).concat(noComplete), outfit => outfit)
    } else return outfits
  }

  store(data = null, options = null) {
    const upload = new Upload(this.__data(data), this, [ 'seasonID', 'complete', 'image' ])

    return new Promise((resolve, reject) =>
      axios.post(`${process.env.API_SERVER}/${this.table}`, upload.data, {
          headers: {
            'Content-Type': 'multipart/form-data',
            'X-ACCESS-KEY': localStorage.getItem((require('app/vue/store').default).getters.cookieAccessKey)
          }
        })
        .then((response) => resolve(response && response.data && response.data.data ? response.data.data : response))
        .catch((error) => reject(error))
    )
  }
}

export default Outfit
