import {__createError, __options} from 'app/http/http-utils';
import {Tools}                    from '../../config/packages/ssf';
import configAttributes           from '../../config/auth';

class UserHelpController extends Tools.Api {
  constructor(attributes = configAttributes) {
    super(attributes);
  }

  request(method, path, options = {}) {
    options = __options(options);
    return new Promise((resolve, reject) => {
      options.loading();
      super.request(method, path, options)
        .then((response) => resolve(response))
        .catch((error) => reject(__createError(error)))
        .finally(() => options.loading(false))
    })
  }

  logged() {
    return this.request('GET', '/logged')
      .then((data) => !!data.logged)
      .catch(() => false)
  }

  current() {
    return this.request('GET', '/logged')
      .then((data) => data.user || data)
      .catch(() => false)
  }

}

export default new UserHelpController()
