const scroll = (options = { speed: 1000, offset: 0, animate: true }) => {
  if (options.animate) {
    $('html, body').animate({
      scrollTop: options.value ? options.to : $(options.to).offset().top - (40 + options.offset)
    }, options.speed);
  } else $(window).scrollTop(options.value ? options.to : $(options.to).offset().top - (40 + options.offset))
};

const phone = {
  format(value, separator = '.', country = null) {
    if (value === null) return value

    value = value.replace(country, 'COUNTRY')
    value = value.replace(/[^0-9\s]/gi, '')

    if (value[0] !== "0")
      value = `0${value}`

    value = value.substr(0, 10).replace(/(.{2})(?!$)/g, `$1${separator}`);
    return !!country ? `${country}${value.substr(1)}` : value
  }
}

const jsonParse = (text) => {

  if ((Array.isArray(text) || text instanceof Object) && !(text instanceof String))
    return text

  try {
    return (/^[\],:{}\s]*$/.test(text.replace(/\\["\\\/bfnrtu]/g, '@').replace(/"[^"\\\n\r]*"|true|false|null|-?\d+(?:\.\d*)?(?:[eE][+\-]?\d+)?/g, ']').replace(/(?:^|:|,)(?:\s*\[)+/g, ''))) ? JSON.parse(text) : null
  } catch (e) {
    return null
  }
};

const combine = (keys, values) => {
  let new_array = {}, keyCount = keys.length, i;
  // input sanitation
  if (!keys || !values || keys.constructor !== Array || values.constructor !== Array) {
    return false;
  }
  // number of elements does not match
  if (keyCount !== values.length) {
    return false;
  }
  for (i = 0; i < keyCount; i++) {
    new_array[keys[i]] = values[i];
  }
  return new_array;
}


const renameKey = function (object, old, key) {
  if (old !== key) {
    Object.defineProperty(object, key, Object.getOwnPropertyDescriptor(object, old));
    delete object[old];
  }
  return object
}

const clone = (object) => jsonParse(JSON.stringify(object))

const key = (item) => btoa(encodeURI(`${item.name || item || 'name'}-${item.id || -1}`))

const hot = () => {
  let moment = (require('./date').default).moment, today = moment(),
    min = moment(`${today.year()}/06/01`),
    max = moment(`${today.year()}/09/20`);
  return today.unix() >= min.unix() && today.unix() <= max.unix()
}

export {
  scroll,
  jsonParse,
  combine,
  renameKey,
  clone,
  key,
  hot,

  phone,
}
