/**
 * File app/store/index.js
 *
 * Ce fichier contient les données vuex
 *
 * @author sofianeakbly
 **/

import Vue  from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const registerModules = () => {
  const requireContext = require.context('./modules', false, /.*\.js$/)

  const modules = requireContext.keys()
    .map(file =>
        requireContext(file)
      // [file.replace(/(^.\/)|(\.js$)/g, ''), requireContext(file)]
    )

  const keys = [ 'actions', 'getters', 'mutations', 'state' ]

  let methods = {}

  keys.forEach(key => {
    if (!methods[key])
      methods[key] = {}

    modules.forEach(module => {
      let data = module.default[key]
      methods[key] = { ...methods[key], ...data }
    })

  })

  return methods
}

let methods = registerModules(), mutationMethods = methods.mutations, actionMethods = methods.actions,
  stateMethods = methods.state, getterMethods = methods.getters

const state = {
  loading: false, // Le formulaire est envoyé
  preload: true, // La page est en train de charger

  variables: {
    storage: {
      ootds       : 'ootds',
      'access-key': 'x-access-key'
    },
  },


  ...stateMethods
};

const mutations = {
  ...{
    setLoading(state, loading) {
      state.loading = loading
    },

    setPreload(state, preload) {
      state.preload = preload
    }
  },

  ...mutationMethods
};

const actions = {

  loading(context, loading) {
    context.commit('setLoading', loading)
  },

  preloading(context, preload) {
    context.commit('setPreload', preload)
  },

  ...actionMethods

};

const getters = {
  APP_NAME       : () => process.env.APP_NAME,
  APP_MAINTENANCE: () => process.env.APP_MAINTENANCE,
  APP_ENV        : () => process.env.APP_ENV || 'dev', // Environnement de l'application,
  API_SERVER     : () => process.env.API_SERVER || 'http://localhost:4001', // Adresse du serveur de l'API
  TOKEN_NAME     : () => process.env.TOKEN_NAME || 'cloufit_token', // Nom du cookie pour stocker le token,

  loading  : state => state.loading,
  preload  : state => state.preload,
  variables: state => state.variables,

  cookieAccessKey: state => `cookie:${process.env.APP_NAME}:${state.variables.storage['access-key']}`,

  storage: state => {
    return { prefix: `storage:${process.env.APP_NAME}:` }
  },
  ...getterMethods
}


export default new Vuex.Store({
  state,
  mutations,
  actions,
  getters
})
