import VueRouter from 'vue-router'
import Router    from 'vue-routisan'

import { AccessScanner, AccessKeyMiddleware } from '@sofiakb/vue2-pwa-access-key'

const router = (Vue) => {

  Vue.use(VueRouter);

  const $middleware = Vue.prototype.$middleware;

  Router.setViewResolver((component) => (typeof component).toLowerCase() === 'string' ? require('@/views/' + component).default : component);

  Router.group({ beforeEnter: AccessKeyMiddleware }, () => {
    /* Page d'accueil */
    Router.view('/', 'home').name('home');
    /* Page de déconnexion */
    Router.view('/logout', 'auth/logout').name('logout')
  });

  Router.view('/access/scanner', AccessScanner).name('access.scanner')
  // Router.view('/access/scanner', require('@/components/commons/scan/access-scanner').default).name('access.scanner')

  // Router.group({ beforeEnter: $middleware.guest }, () => {
  //   /* Page de connexion */
  //   Router.view('/login', 'auth/login').name('login');
  //   /* Page d'inscription*/
  //   Router.view('/register', 'auth/register').name('register');
  //   /* Page de mot de passe oublié */
  //   Router.view('/password/forgot', 'auth/password/forgot').name('password.forgot');
  //   /* Page de reset mot de passe */
  //   Router.view('/password/reset/:email', 'auth/password/reset').name('password.reset')
  // });

  Router.redirect('*', '/');

  return new VueRouter({
    routes: Router.all(),
    mode  : process.env.ROUTER_MODE,
    base  : process.env.PUBLIC_PATH
  })
}

export default router
