// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue        from 'config/packages/vue'
import {App, Npm} from 'config/packages'
import prototypes from "config/prototype"

import VueApp from '@/App'

window._ = require('lodash');
window.$ = require('jquery');

import './app/vue/utils/native/string'
import 'app/vue/register/serviceWorker'

import './public/static/css/main.css'
import './public/static/css/colors.css'

Vue.use(Npm.Storage, { prefix: App.store.getters.storage.prefix });

if (App.store.getters.APP_ENV === 'dev') {
  Vue.config.devtools = true;
  Vue.config.performance = true
} else if (App.store.getters.APP_ENV === 'production' || App.store.getters.APP_ENV === 'pre-production') {
  Vue.config.productionTip = false;
  Vue.config.silent = true;
}

Vue.prototype = prototypes(Vue, App, Npm);

new Vue({
  el    : '#app',
  store : App.store,
  router: App.router(Vue),
  render: h => h(VueApp)
}).$mount('#app');
