<template>
  <ssf-modal :name="name"
             height="auto"
             :adaptive="true"
             :reset="true"
             :scrollable="true"
             class="py-2"
             :width="width"
             classes="rounded bg-transparent modal-show-outfit"
             body-classes=""
             footer-classes="justify-content-center bg-white border-0 rounded bg-black footer-outfit"
             @before-open="beforeOpen">

    <template slot="body" v-if="outfit">

      <ssf-row class="mx-0 bg-white rounded">
        <img :src="src(outfit)" alt="Image principale" class="mx-auto main-img">
        <img :src="src(outfit)" alt="Fond d'écran" class="w-100 position-absolute bg-img">
      </ssf-row>

      <ssf-container name="form-outfit" class="p-3 bg-black my-1 rounded" not-full>
        <form @submit.prevent="save">
          <ssf-form-group name="select"
                          v-model="outfit.seasonID"
                          select
                          :option-items="seasons"
                          option-field="id"
                          option-label="label"
                          col="col-12"
                          row
                          label="| Saison"
                          form-group-class="mb-1"
                          :icon="{icon: outfit.season.icon, type: 'la'}"
                          @input="onInput"
                          input-class="border-top-0 border-left-0 border-right-0 rounded-0 pl-0"/>
        </form>
      </ssf-container>

    </template>

    <template slot="footer">

      <ssf-row>

        <ssf-col size="4">
          <btn-action icon="trash" class="bg-transparent color-2" @click="remove"/>
        </ssf-col>

        <ssf-col size="4">
          <btn-action icon="save" class="bg-transparent text-success" @click="save"/>
        </ssf-col>

        <ssf-col size="4">
          <btn-action :icon="`${outfit && outfit.complete ? 'check' : 'shopping-cart'}`"
                      class="bg-transparent color-1" @click="complete"/>
        </ssf-col>

      </ssf-row>

    </template>

    <btn-action icon="times" class="position-absolute btn-close m-1" @click="$modal.hide(name)"/>

  </ssf-modal>
</template>

<script>
  import BtnAction      from "./btn-action";
  import { find }       from "lodash";
  import { mapGetters } from "vuex";

  export default {
    name: "modal-show-outfit",

    components: { BtnAction },

    props: {
      src : { type: Function, required: true },
      name: { type: String, required: false, default: 'modal-show-outfit' }
    },

    computed: {
      ...mapGetters({
        seasons: 'seasons'
      })
    },

    data() {
      return {
        outfit: null,
        width : Math.min(...[ 400, $(window).width() - 16 ])
      }
    },

    methods: {

      beforeOpen(events) {
        this.width = Math.min(...[ 400, $(window).width() - 30 ])
        if (events.params && events.params.outfit)
          this.outfit = events.params.outfit;
        this.$forceUpdate()
      },

      complete() {
        this.outfit.complete = !this.outfit.complete
        // return this.save()
      },

      remove() {
        this.outfit.destroy()
          .then(() => {
            this.$emit('removed', this.outfit)
            this.close()
          })
          .catch((error) => this.$message.error(error.message))
      },

      save() {
        this.outfit.update()
          .then(() => {
            this.$emit('save', this.outfit)
            this.close()
          })
          .catch((error) => this.$message.error(error.message))
      },

      close() {
        this.$modal.hide(this.name)
      },

      /* HANDLERS */

      onInput(value) {
        this.outfit.season = find(this.seasons, season => season.id === value)
        this.$forceUpdate()
      }

    }
  }
</script>

