        &.active {
          font-size: 13px !important;
          margin-left: 0 !important;
        }
      }

      .ssf-select-input-container {
        color: #fff;

        &.open {
          /*background: #000 !important;*/
          color: inherit !important;
        }

        /* .ssf-value-item.selected {
           background: #ffffff !important;
           color: var(--color-5) !important;
         }*/
      }

      label:not(.active) + .ssf-select-container {
        z-index: 1050 !important;
      }

      label {
        background: #000 !important;
        /*background: var(--color-5) !important;*/
        color: #FFF !important;
      }

      .ssf-form-control {
        font-size: 16px !important;
      }
    }

    .v--modal-overlay[data-modal=modal-show-outfit] {
      background: rgba(0, 0, 0, .5);
    }

    $btnActionSize: 40px;

    .btn-action {
      height: $btnActionSize;
      width: $btnActionSize;
      color: #ffffff;
      border-radius: 50% !important;
      margin-left: auto;
      margin-right: auto;
      border: none;

      &:focus {
        outline: none !important;
      }
    }

    .btn-close {
      z-index: 1051;
      top: 0;
      right: 0;
      height: 35px;
      width: 35px;
      background-color: rgba(0, 0, 0, .7) !important
    }

    .footer-outfit {
      padding: .75rem;
    }
  }

</style>
