import { __options, __createError } from 'app/http/http-utils';
import { Tools }                    from 'config/packages/ssf';
import configAttributes             from 'config/api';

class Controller extends Tools.Api {
  constructor(attributes = configAttributes) {
    super(attributes);
  }

  request(method, path, options = {}) {
    options = __options(options);
    return new Promise((resolve, reject) => {
      options.loading();
      super.request(method, path, options)
        .then((response) => resolve(response))
        .catch((error) => reject(__createError(error)))
        .finally(() => options.loading(false))
    })
  }

  fetch(url, options = {}) {
    return this.request('GET', url, options)
  }

  fetchBy(url, parameter, options = {}) {
    return this.fetch(`${url}/${parameter}`, options)
  }

  create(url, data, options = {}) {
    return this.request('POST', url, { ...options, ...{ data } })
  }

  upload(url, data, options = {}) {
    return this.request('POST', url, { ...options, ...data })
  }

  update(url, id, data, options = {}) {
    return this.request('PUT', `${url}/${id}`, { ...options, ...{ id, data } })
  }

  destroy(url, id, options = {}) {
    return this.request('DELETE', `${url}/${id}`, { ...options, ...{ id } })
  }

  search(url, data, options) {
    return this.request('POST', url, { ...options, ...{ data } })
  }

  paginate(url, page = 0, limit = 18, options = {}) {
    return this.request('GET', `${url}/page/${page}?limit=${limit}`, options)
  }
}

export default Controller
