import Model from "./model";

class Season extends Model {

  static icon = {
    name: 'test',
    type: 'la'
  }

  attributes = {
    id   : { prop: '_id', comment: "Identifiant" },
    icon : { prop: 'icon', comment: "Icône" },
    name : { prop: 'name', comment: "Nom court" },
    label: { prop: 'label', comment: "Saison" },
  }

  casts = {
    created: 'date',
    updated: 'date'
  }

  constructor(options = {}) {
    super(options);
    this.model = eval(this.__resolve.model(options.name || this.constructor.name))
  }

  static WINTER = 1
  static SUMMER = 2
  static MIX = 3

  static CURRENT = require('app/vue/utils').hot() ? this.SUMMER : this.WINTER
}

export default Season
