<template>
  <modal
    :name="name"
    :delay="delay"
    :resizable="resizable"
    :adaptive="adaptive"
    :draggable="draggable"
    :scrollable="scrollable"
    :reset="reset"
    :overlayTransition="overlayTransition"
    :transition="transition"
    :clickToClose="clickToClose"
    :classes="options.classes.modal"
    :styles="styles"
    :minWidth="minWidth"
    :minHeight="minHeight"
    :maxWidth="maxWidth"
    :maxHeight="maxHeight"
    :width="width"
    :height="height"
    :pivotX="pivotX"
    :pivotY="pivotY"
    @before-open="(event) => $emit('before-open', event)"
    @opened="(event) => $emit('opened', event)"
    @closed="(event) => $emit('closed', event)"
    @before-close="(event) => $emit('before-close', event)">

    <ssf-container name="modal-header" :class="options.classes.header" v-if="hasSlot('header')">
      <slot name="header"/>
    </ssf-container>

    <ssf-container name="modal-body" :class="options.classes.body" v-if="hasSlot('body')">
      <slot name="body"/>
    </ssf-container>

    <ssf-container name="modal-footer" :class="options.classes.footer" v-if="hasSlot('footer') || footerAction">

      <ssf-row v-if="footerAction">

        <ssf-col xs="6" class="border-right border-white" no-padding>
          <button type="button" class="btn btn-block text-danger bg-transparent" @click="$emit('cancel')">
            <ssf-icon :icon="cancelIcon" size="2x" la/>
          </button>
        </ssf-col>
        <ssf-col xs="6" class="border-left border-white" no-padding>
          <button type="button" class="btn btn-block text-success bg-transparent" @click="$emit('save')">
            <ssf-icon :icon="saveIcon" size="2x" la/>
          </button>
        </ssf-col>

      </ssf-row>

      <slot name="footer" v-else/>

    </ssf-container>

    <slot/>

  </modal>
</template>

<script>


  import { hasSlot } from "vue-ssf-element/utils";

  const vueJsPackage = 'vue-js-modal',
    classes = {
      modal: 'v--modal',
      header: 'v--modal-header',
      body  : 'v--modal-body',
      footer: 'v--modal-footer',
    }


  try {
    let VModal = require('vue-js-modal').default
    VModal = undefined
  }
  catch (e) {
    const errorMessage = `Package ${vueJsPackage} is missing. \n\nRun : npm install --save ${vueJsPackage}.`
    console.warn(errorMessage)
    throw new Error(errorMessage)
  }

  export default {
    name: "ssf-modal",

    props: {
      name             : { required: true, type: String },
      delay            : { type: Number, default: 0 },
      resizable        : { type: Boolean, default: false },
      adaptive         : { type: Boolean, default: false },
      draggable        : { type: [Boolean, String], default: false },
      scrollable       : { type: Boolean, default: false },
      reset            : { type: Boolean, default: false },
      overlayTransition: { type: String, default: 'overlay-fade' },
      transition       : { type: String },
      clickToClose     : { type: Boolean, default: true },
      classes          : { type: [String, Array], default: 'v--modal' },
      styles           : { type: [String, Array, Object] },
      minWidth         : { type: Number, default: 0 },
      minHeight        : { type: Number, default: 0 },
      maxWidth         : { type: Number, default: Number.MAX_SAFE_INTEGER },
      maxHeight        : { type: Number, default: Number.MAX_SAFE_INTEGER },
      width            : { type: [Number, String], default: 600 },
      height           : { type: [Number, String], default: 300 },
      pivotX           : { type: Number, default: 0.5, },
      pivotY           : { type: Number, default: 0.5 },

      footerAction: { type: [Boolean, String], required: false, default: false },
      cancelIcon  : { type: String, required: false, default: 'times' },
      saveIcon    : { type: String, required: false, default: 'save' },

      headerClasses: { type: String, required: false, default: 'v--modal-header border-bottom' },
      bodyClasses  : { type: String, required: false, default: 'v--modal-body py-4' },
      footerClasses: { type: String, required: false, default: 'v--modal-footer py-3' },

    },

    watch: {
      classes: {
        immediate: true,
        handler(val, oldVal) {
          this.options.classes.modal = `${classes.modal} ${this.classes.replaceAll(classes.modal, '')}`
        }
      },
      headerClasses: {
        immediate: true,
        handler(val, oldVal) {
          this.editClass('header')
        }
      },
      bodyClasses  : {
        immediate: true,
        handler(val, oldVal) {
          this.editClass('body')
        }
      },
      footerClasses: {
        immediate: true,
        handler(val, oldVal) {
          this.editClass('footer')
        }
      },
    },

    data() {
      return {
        options: {
          classes: {
            header: null,
            body  : null,
            footer: null,
          }
        }
      }
    },

    methods: {
      hasSlot(name = 'default') {
        return hasSlot(this, name)
      },
      editClass(type) {
        this.options.classes[type] = `${classes[type]} ${this[`${type}Classes`].replaceAll(classes[type], '')}`
      }
    }
  }
</script>

<style lang="scss">
  /* Hide scrollbar for Chrome, Safari and Opera */
  .v--modal-overlay.scrollable::-webkit-scrollbar {
    display: none;
  }

  /* Hide scrollbar for IE, Edge and Firefox */
  .v--modal-overlay.scrollable {
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
  }

</style>
