<template>
  <div class="loader rotating-plane"
       :style="`{
       --reset:0;
    --loader-width: ${size};
    --loader-height: ${size};
    --loader-color-primary: ${parent.colorPrimary};
    --loader-color-secondary: ${parent.colorSecondary};
    --line-width: ${stroke};
    --animation-duration: ${duration};
    --loader-initial-scale: 0.1;
  }`"></div>
</template>

<script>
  export default {
    name: "rotating-plane",

    computed: {
      parent() {
        return this.$parent.$props
      },

      size() {
        const size = this.parent.size;
        return typeof size === 'string' ? size : `${size}px`
      },

      stroke() {
        const stroke = this.parent.stroke;
        return typeof stroke === 'string' ? stroke : `${stroke}px`
      },

      duration() {
        const duration = this.parent.duration;
        return typeof duration === 'string' ? duration : `${duration}s`
      }

    }
  }
</script>

<style scoped lang="scss">

  .loader {
    &,
    &:before,
    &:after {
      content: '';
      box-sizing: border-box;
      -webkit-box-flex: 0;
      -ms-flex-positive: 0;
      flex-grow: 0;
      -ms-flex-negative: 0;
      flex-shrink: 0;
    }

    @-webkit-keyframes rotateplane {
      0% {
        -webkit-transform: perspective(var(--perspective-value)) rotateX(0deg) rotateY(0deg);
        transform: perspective(var(--perspective-value)) rotateX(0deg) rotateY(0deg);
      }
      50% {
        -webkit-transform: perspective(var(--perspective-value)) rotateX(-180.1deg) rotateY(0deg);
        transform: perspective(var(--perspective-value)) rotateX(-180.1deg) rotateY(0deg);
      }
      100% {
        -webkit-transform: perspective(var(--perspective-value)) rotateX(-180deg) rotateY(-179.9deg);
        transform: perspective(var(--perspective-value)) rotateX(-180deg) rotateY(-179.9deg);
      }
    }

    @keyframes rotateplane {
      0% {
        -webkit-transform: perspective(var(--perspective-value)) rotateX(0deg) rotateY(0deg);
        transform: perspective(var(--perspective-value)) rotateX(0deg) rotateY(0deg);
      }
      50% {
        -webkit-transform: perspective(var(--perspective-value)) rotateX(-180.1deg) rotateY(0deg);
        transform: perspective(var(--perspective-value)) rotateX(-180.1deg) rotateY(0deg);
      }
      100% {
        -webkit-transform: perspective(var(--perspective-value)) rotateX(-180deg) rotateY(-179.9deg);
        transform: perspective(var(--perspective-value)) rotateX(-180deg) rotateY(-179.9deg);
      }
    }

    &.rotating-plane {
      --perspective-value: calc(3 * var(--loader-width, 100px));
      width: var(--loader-width, 100px);
      height: var(--loader-height, 100px);
      background: var(--loader-color-primary, #00f);
      -webkit-animation: rotateplane var(--animation-duration, 1s) infinite ease-in-out;
      animation: rotateplane var(--animation-duration, 1s) infinite ease-in-out;
    }
  }


</style>
