<template>
  <div id="app">
    <div id="iosStatusBar" class="bg-transparent"></div>

    <!-- APPLICATIN IS UP -->
    <div class="h-100 w-100" v-if="!maintenance">
      <!-- MAIN CONTAINER -->
      <div class="main-container">
        <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut">
          <router-view v-if="loaded" class="main-container"/>
        </transition>
      </div>

      <!-- PRE LOAD -->
      <pre-load/>

      <!-- FORM LOAD-->
      <form-load/>

      <!-- FOOTER -->
      <!--      <x-footer/>-->
    </div>

    <!-- MAINTENANCE MODE -->
    <maintenance v-else-if="maintenance"/>

  </div>
</template>

<script>

  import FormLoad    from '@/components/commons/form-load'
  import PreLoad     from '@/components/commons/pre-load'
  import XFooter     from '@/components/commons/partials/x-footer'
  import Maintenance from '@/components/maintenance'

  export default {
    name: 'App',

    metaInfo: {
      // if no subcomponents specify a metaInfo.title, this title will be used
      title: 'Cloufit',
      // all titles will be injected into this template
      titleTemplate: '%s | Cloufit'
    },

    components: {
      Maintenance,
      FormLoad,
      PreLoad,
      XFooter,
    },

    data() {
      return {
        loading: true,
      }
    },

    computed: {
      loaded() {
        return /*!this.$store.getters.preload &&*/ !this.loading
      },

      maintenance() {
        return this.$store.getters.APP_MAINTENANCE && (this.$store.getters.APP_MAINTENANCE === 'true' || this.$store.getters.APP_MAINTENANCE === true)
      }
    },

    async created() {
      await this.fetchSeasons()
      this.loading = false
    },

    methods: {

      fetchSeasons() {
        this.$store.dispatch('fetchAllSeasons')
      }

      /*onCustomAction(action) {
       return eval(this[action]())
       },

       navigate(name) {
       return this.$router.push({ name })
       },*/

    }
  }
</script>

