import cookie from "json-cookie";

import store       from "app/vue/store";
import {jsonParse} from "app/vue/utils";

import UserHelpController from 'app/http/controllers/user-help-controller'

const storageName = `${store.getters.storage.prefix}user`;

class UserAuth {

  static token = {
    get    : () => cookie.get(store.getters.TOKEN_NAME) || null,
    store  : (token) => cookie.set(store.getters.TOKEN_NAME, token.content,
      { secure: false, expires: (parseInt(token.expires) / 86400) + 1 }),
    destroy: () => cookie.delete(store.getters.TOKEN_NAME)
  };

  static storage = {
    get    : () => jsonParse(localStorage.getItem(storageName)),
    store  : (user) => localStorage.setItem(storageName, JSON.stringify(user)),
    destroy: () => localStorage.removeItem(storageName)
  };

  static is = {
    authenticate  : () => this.token.get() && !(this.token.get().trim() === ''),
    logged        : async () => this.is.authenticate() && await UserHelpController.logged(),
    strongPassword: (password) => /(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*?[\/+~"'{}()|#`_\[\]=%§.,:;?!@$^&*-]).{8,}/.test(password)
  };

  static get = {
    storage: () => this.storage.get(),
    current: async () => await UserHelpController.current()
  };

  static setTokenAndStorage(token, user) {
    this.token.store(token);
    this.storage.store(user);
  }

  static deleteTokenAndStorage() {
    this.token.destroy();
    this.storage.destroy();
  }

  static isStrongPassword(password) {
    return /(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*?[\/+~"'{}()|#`_\[\]=%§.,:;?!@$^&*-]).{8,}/.test(password);
  }
}

export default UserAuth
