import { fetchAllSeasons } from "repositories/season-repository";

import { find } from 'lodash'

const fetchAll = ({ commit }) => fetchAllSeasons()
  .then(seasons => commit('SET_SEASONS', seasons))

const state = {
  seasons: null,
}

const actions = {
  fetchAllSeasons: fetchAll,
}

const getters = {
  seasons: state => state.seasons,
  SEASON: state => state.SEASON,
}

const mutations = {
  SET_SEASONS:(state, seasons) => state.seasons = seasons

}


export default {
  state,
  actions,
  getters,
  mutations
}
