<template>
  <ssf-modal :name="name"
             :adaptive="true"
             :reset="true"
             class="py-2"
             :width="width"
             :pivotY=".5"
             classes="rounded"
             :footer-action="false"
             body-classes=""
             @before-open="beforeOpen">

    <template slot="body">
      <div class="row w-100 mx-0" v-if="outfit">
        <img :src="src(outfit)" alt="Image principale" class="mx-auto main-img">
        <img :src="src(outfit)" alt="Fond d'écran" class="w-100 position-absolute bg-img">
      </div>
    </template>

    <button
      class="position-absolute waves-effect waves-light btn-close rounded-circle p-0 full-flex border-0 m-1 text-white mx-auto"
      @click="$emit('close')">
      <ssf-icon icon="times" size="lg" la/>
    </button>

  </ssf-modal>
</template>

<script>
  export default {
    name: "modal-ootd",

    props: {
      src : { type: Function, required: true },
      name: { type: String, required: false, default: 'modal-ootd' }
    },

    data() {
      return {
        outfit: null,
        width : Math.min(...[ 400, $(window).width() - 16 ])
      }
    },

    methods: {

      beforeOpen(events) {
        this.width = Math.min(...[ 400, $(window).width() - 30 ])
        if (events.params && events.params.outfit)
          this.outfit = events.params.outfit;
        this.$forceUpdate()
      },
    }
  }
</script>

<style scoped>

  .btn-close {
    z-index: 1051;
    bottom: 0;
    right: 0;
    left: 0;
    height: 35px;
    width: 35px;
    background-color: rgba(0, 0, 0, .7) !important
  }

</style>
