<template>
  <ssf-modal :name="name"
             adaptive
             reset
             scrollable
             body-classes="p-4"
             @before-open="beforeEnter"
             height="auto">

    <template slot="body">
      <ssf-container name="form-add-container">

        <form id="formAddContainer" @submit.prevent="save">

          <ssf-form-group name="select"
                          v-model="outfit.seasonID"
                          select
                          :option-items="seasons"
                          option-field="id"
                          option-label="label"
                          col="12"
                          row
                          label="| Saison"
                          :icon="{icon: 'sun', type: 'la'}"
                          @input="onSeasonChange"/>

          <ssf-form-group name="image"
                          file-accepted="image/*"
                          mimes="gif, jpeg, png"
                          col="12"
                          row
                          @input="onFileInput"
                          file-preview
                          type="file"/>

          <ssf-form-group name="complete"
                          v-model="outfit.complete"
                          col="12"
                          row
                          type="switch"
                          label="&nbsp;&nbsp;La tenue est complète ?"/>

          <ssf-row>
            <ssf-col size="12">
              <ssf-container class="ssf-form-group text-center">
                <button class="btn bg-color-1 m-0 text-white" :disabled="!valid" type="submit">
                  Ajouter
                  <ssf-icon icon="layer-plus" light/>
                </button>
              </ssf-container>
            </ssf-col>
          </ssf-row>

        </form>

      </ssf-container>
    </template>

  </ssf-modal>
</template>

<script>

  import Outfit         from "app/models/outfit";
  import { mapGetters } from "vuex";
  import { find }       from "lodash";
  import Upload         from "app/models/upload";

  export default {
    name: "modal-add-outfit",

    props: {
      name: { type: String, required: false, default: 'modal-add-outfit' }
    },

    data() {
      return {
        outfit: {},
        keys  : [ 'seasonID', 'image', 'complete' ]
      }
    },

    created() {
      this.outfit = { complete: false, image: null, seasonID: null }
    },

    computed: {
      ...mapGetters({
        seasons: 'seasons'
      }),

      valid() {
        return this.keys.map(key => this.outfit[key]).filter(item => !(typeof item === 'undefined' || item === null) && (typeof item !== 'string' || item.trim() !== '')).length === this.keys.length
      }
    },

    watch: {},

    methods: {
      beforeEnter() {
        if (this.$store.getters.APP_ENV !== 'dev' && this.$store.getters.APP_ENV !== 'pre-production')
          this.$modal.hide(this.name)
        this.outfit = new Outfit({ attributes: { complete: false, image: null, season_id: null } })
      },

      save() {
        const upload = new Upload(this.keys.map(key => this.outfit[key]), this.outfit, [ 'seasonID', 'complete', 'image' ])
        if (this.valid)
          this.outfit.store()
            .then(() => {
              this.$message.success('La tenue a été ajoutée avec succès')
              this.$modal.hide(this.name)
            })
        else this.$message.error('Merci de remplir le formulaire')
      },

      /* HANDLERS */

      onSeasonChange(value) {
        this.outfit.season = find(this.seasons, season => season.id === value)
        this.$forceUpdate()
      },

      onFileInput(file) {
        if (file) {
          this.outfit.image = file.data
        }
      },

    }
  }
</script>

<style lang="scss">

  .ssf__section--form-add-container {

    .ssf-file-selector.ssf-form-control {

      .selector {
        background: transparent !important;
        color: #0f0f0f !important;
      }

    }

  }


</style>
