<template>
  <ssf-container name="home" id="homeIndex" class="py-2" v-if="outfits && render">
    <stack :column-min-width="171" :gutter-width="0" :gutter-height="16" monitor-images-loaded>
      <stack-item v-for="(outfit, index) in outfits"
                  :key="key(outfit)"
                  :data-complete="`${outfit.complete}`"
                  :data-current-season="`${isCurrentSeason(outfit)}`"
                  class="cursor-pointer"
                  style="transition: transform 300ms">
        <div class="px-2">
          <img :id="`img-${index}`"
               :src="src(outfit)"
               :alt="outfit.alt"
               :ref="`imageOutfit-${index}`"
               class="w-100 h-100 rounded"
               @click="$modal.show(modals.showOutfit.name, { outfit })"/>
        </div>
      </stack-item>
    </stack>

    <button class="button-random-ootd position-fixed mx-auto btn p-0 m-0 bg-color-1 rounded-circle full-flex text-white"
            @click="onRandomOotd">
      <ssf-icon icon="random" type="la" size="2x"/>
    </button>

    <button class="button-add-ootd position-fixed mx-auto btn p-0 m-0 bg-color-3 rounded-circle full-flex text-white"
            @click="$modal.show(modals.addOutfit.name)"
            v-if="isDev">
      <ssf-icon icon="layer-plus" light size="1x5"/>
    </button>

    <!-- MODAL SHOW OUTFIT -->
    <modal-show-outfit :src="src" :name="modals.showOutfit.name" @save="onSave" @removed="onDelete"/>

    <!-- RANDOM OUTFIT -->
    <modal-ootd :src="src" @close="onCloseOotd" :name="modals.ootd.name"/>


    <!-- MODAL ADD OUTFIT -->
    <modal-add-outfit :name="modals.addOutfit.name"
                      v-if="isDev"/>

  </ssf-container>
</template>

<script>
  import { mapGetters, mapState }          from "vuex";
  import Outfit                            from "app/models/outfit";
  import { Stack, StackItem }              from "vue-stack-grid";
  import { key }                           from "app/vue/utils";
  import Season                            from "app/models/season";
  import { filter, find, shuffle, sample } from "lodash";
  import ModalOotd                         from "@/views/home/components/includes/modal-ootd";
  import ModalShowOutfit                   from "@/views/home/components/includes/modal-show-outfit";
  import ModalAddOutfit                    from "./includes/modal-add-outfit";

  export default {
    name: "home-index",

    components: { ModalAddOutfit, ModalShowOutfit, ModalOotd, Stack, StackItem },

    metaInfo: {
      title: 'Accueil',
    },

    created() {
      this.run()
    },

    computed: {
      ...mapState({
        storageOotds: state => state.variables.storage.ootds,
      }),
      ...mapGetters({
        outfitsGetters: 'outfits',
      }),

      completed() {
        return filter(this.outfits, outfit => outfit.complete)
      },

      storage() {
        return {
          ootds: {
            get   : () => this.$localStorage.get(this.storageOotds),
            set   : (ootds) => this.$localStorage.set(this.storageOotds, ootds),
            remove: () => this.$localStorage.remove(this.storageOotds),
          }
        }
      },

      isDev() {
        return this.$store.getters.APP_ENV === 'dev' || this.$store.getters.APP_ENV === 'pre-production'
      }
    },

    data() {
      return {
        outfits: null,
        render : true,

        modals: {
          ootd      : { name: 'modal-ootd' },
          showOutfit: { name: 'modal-show-outfit' },
          addOutfit : { name: 'modal-add-outfit' },
        }
      }
    },

    watch: {
      outfitsGetters: {
        handler(val, oldVal) {
          this.outfits = Outfit.sort(val)
        }
      },

      outfits() {
        this.render = false;
        setTimeout(() => this.render = true, 200);
        return true
      }
    },

    methods: {
      fetchRepositories() {
        this.$store.dispatch(`fetchAllOutfits`)
      },

      key: (outfit) => key({ name: outfit.image, id: outfit.id }),

      src: (outfit) => Outfit.src(outfit.image),

      isCurrentSeason(outfit) {
        return outfit.complete && (Season.CURRENT === outfit.season.id || outfit.season.id === Season.MIX)
      },

      randomOotd() {
        let season = find(this.$store.getters.seasons, season => season.id === Season.CURRENT),
          outfits = filter(this.completed, outfit => outfit.season.id === season.id || outfit.season.id === Season.MIX),
          ootd, ootds = this.storage.ootds.get();

        if (ootds === null || outfits.length === ootds.length)
          ootds = [];
        ootd = shuffle(outfits.filter(outfit => !ootds.map(style => style.id).includes(outfit.id)));

        if (!ootd || ootd.length === 0) {
          this.storage.ootds.remove();
          return this.randomOotd()
        }

        if ((ootd = sample(ootd))) {
          ootds.push(ootd);
          this.storage.ootds.set(ootds)
        }

        return ootd
      },

      run() {
        this.fetchRepositories()
      },

      /* HANDLERS */
      onRandomOotd() {
        this.modals.ootd.random = this.randomOotd();
        this.$modal.show(this.modals.ootd.name, { outfit: this.modals.ootd.random })
      },

      onCloseOotd() {
        this.modals.ootd.random = null
        this.$modal.hide(this.modals.ootd.name)
      },

      async onSave(data) {
        await this.$store.dispatch('editOutfitsIndex', {
          index: this.outfits.findIndex(datum => datum.id === data.id),
          data
        })
        this.outfits = Outfit.sort(this.outfits)
      },

      async onDelete(data) {
        await this.$store.dispatch('editOutfitsIndex', {
          index: this.outfits.findIndex(datum => datum.id === data.id),
          data : null
        })
        this.outfits = Outfit.sort(this.outfits)
      }
    }
  }
</script>

<style lang="scss">

  .main-img, .bg-img {
    max-width: 100%;
    max-height: 50vh;
  }

  .main-img {
    z-index: 2;
  }

  .bg-img {
    filter: blur(1px) brightness(0.4) grayscale(50%) opacity(25%) sepia(60%);
    z-index: 1;
  }

  .ssf__section--home {

    [data-complete=false] {
      img {
        filter: brightness(0.6) grayscale(50%) opacity(25%) sepia(70%);
      }
    }

    [data-current-season=false]:not([data-complete=false]) {
      img {
        filter: brightness(0.8) opacity(35%);
      }
    }

    .button-random-ootd,
    .button-add-ootd {
      position: fixed;
      left: 0;
      right: 0;
      bottom: calc(10px + var(--padding-bottom));
      height: 50px;
      width: 50px;
    }

    .button-add-ootd {
      left: inherit;
      right: 15px;
    }
  }

</style>
