import { fetchAllOutfits } from "repositories/outfit-repository";

import { filter } from "lodash";

const fetchAll = ({ commit }) => fetchAllOutfits()
  .then(outfits => commit('SET_OUTFITS', outfits))

const editIndex = ({ commit }, payload) => {
  payload.commit = commit
  commit('SET_OUTFIT_INDEX', payload)
}

const state = {
  outfits: null
}

const actions = {
  fetchAllOutfits : fetchAll,
  editOutfitsIndex: editIndex
}

const getters = {
  outfits: state => state.outfits
}

const mutations = {
  SET_OUTFITS: (state, outfits) => state.outfits = outfits,
  SET_OUTFIT_INDEX(state, payload) {
    let outfits = state.outfits

    if (!Array.isArray(outfits))
      outfits = []

    if (outfits[payload.index])
      outfits[payload.index] = payload.data
    // else outfits.push(payload.data)

    payload.commit('SET_OUTFITS', outfits)
  }
}


export default {
  state,
  actions,
  getters,
  mutations
}
