<template>
  <div class="ssf-input-container ssf-form-number">

    <ssf-input-label
      :label="parent.label"
      :placeholder="placeholder"
      :icon="icon"
      @label-click="onLabelClick"
      :key="`number-label-${parent.label}`"/>

    <!-- TYPE : NUMBER -->
    <input
      class="ssf-form-control"
      :type="inputType"
      :id="parent.name"
      :name="parent.name"
      :value="parent.value"
      :class="parent.inputClass"
      :required="parent.required"
      :disabled="parent.disabled"
      :placeholder="placeholder"
      :autocomplete="autocomplete"
      :inputmode="parent.inputmode ? 'decimal' : ''"
      :step="parent.step"
      :min="parent.min"
      :max="parent.max"
      @input="onInput()"
      @change="$emit('change')"
      @focusin="focused = true"
      @focusout="focused = false"
      @focus="$emit('focus')"
      ref="inputComponent"/>

    <div class="ssf-number-control">
      <div class="control control-more" @click="onInput(1)">
        <span></span>
      </div>
      <div class="control control-less" @click="onInput(-1)">
        <span></span>
      </div>
    </div>

  </div>
</template>

<script>
  import SsfInputLabel from "../input-label";

  export default {
    name: "ssf-input-number",

    components: { SsfInputLabel },

    computed: {

      icon() {
        return this.parent.$data.options.icon
      },

      parent() {
        return this.$parent
      },

      placeholder() {
        const placeholder = this.parent.placeholder, label = this.parent.label;
        return (placeholder === true) ? label : (typeof placeholder).toLowerCase() === 'string' ? placeholder : ''
      },

      autocomplete() {
        const autocomplete = this.parent.autocomplete;
        return `${autocomplete ? autocomplete === true ? this.parent.name : autocomplete : ''}`
      },

      small() {
        return window.innerWidth < 768
      },

      inputType() {
        return this.small ? 'text' : this.parent.type
      }
    },

    methods: {

      onLabelClick() {
        this.$nextTick(() => {
          this.focused = true;
          this.$refs.inputComponent.focus();
          this.$forceUpdate()
        })
      },

      format(value = null) {
        return parseInt(value || this.$parent.value || 0)
        if (!value && !this.$parent.value)
          return 0;
        if (!value)
          value = this.$parent.value;
        value = (value || 0).toString().replace('.', ',');
        value = !value.includes(',') || (value).toString().split(',').length > 1 ? value : parseInt(`${value}00`);
        return (Math.round(value) === value ? parseInt(value) : parseFloat(value))
      },

      increase(value) {
        // console.log(parseInt(this.parent.max), value + parseInt(this.parent.step), Math.min(value + parseInt(this.parent.step), parseInt(this.parent.max)))
        return this.parent.max !== null
          ? Math.min(...[parseInt(value) + parseInt(this.parent.step), parseInt(this.parent.max)])
          : parseInt(value) + parseInt(this.parent.step);
        /*return !this.parent.max || (this.parent.max && value + parseInt(this.parent.step) <= parseInt(this.parent.max))
          ? value + parseInt(this.parent.step)
          : parseInt(this.parent.max) && value + parseInt(this.parent.step) > parseInt(this.parent.max)
            ? parseInt(this.parent.max)
            : 0;*/
      },

      decrease(value) {
        return this.parent.min !== null
          ? Math.max(...[parseInt(value) - parseInt(this.parent.step), parseInt(this.parent.min)])
          : parseInt(value) - parseInt(this.parent.step);
        // return (!this.parent.min && parseInt(this.parent.min) !== 0) || ((this.parent.min || parseInt(this.parent.min) === 0) && value - parseInt(this.parent.step) >= parseInt(this.parent.min)) ? value - parseInt(this.parent.step) : (this.parent.min || parseInt(this.parent.min) === 0) && value - parseInt(this.parent.step) < parseInt(this.parent.min) ? parseInt(this.parent.min) : 0;
      },

      onInput(direction = null) {
        let value = this.format();
        value = Number.isNaN(value) ? Number.isNaN(this.parent.min) ? 0 : this.parent.min : value;
        return direction === 1
          ? this.$emit('input', this.format(this.increase(value)))
          : direction === -1
            ? this.$emit('input', this.format(this.decrease(value)))
            : this.$nextTick(() => {
              this.$emit('input', this.format(this.$refs.inputComponent.value))
            });
      }
    }
  }
</script>

<style lang="scss" scoped>
  $defaultBorder: #ddd;
  $defaultBorderRadius: 4px;

  .ssf-form-number {
    position: relative;


    input[type=number]::-webkit-inner-spin-button,
    input[type=number]::-webkit-outer-spin-button {
      -webkit-appearance: none !important;
      -moz-appearance: none !important;
      margin: 0;
    }

    .ssf-form-control {
      padding-right: 0;
      -webkit-appearance: none;
      //padding-left: 50px;
    }

    label {
      //margin-left: 42px;
    }

    .ssf-form-control:focus + label,
    label.active {
      //margin-left: 42px;
    }

    .ssf-number-control {
      -webkit-user-select: none;
      -moz-user-select: none;
      user-select: none;

      .control {
        position: absolute;
        height: 50%;
        //top: 1px;
        //bottom: 1px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 40px;
        cursor: pointer;
        border-left: 1px solid #ddd;
        //background: #fff;
        transition: all .5s;

        &:hover {
          background: #000;

          &.control-more:before,
          &.control-less:before {
            color: #ffffff;
          }
        }

        &.control-more {
          top: 1px;
          right: 1px;
          border-top-right-radius: $defaultBorderRadius;
          //border-bottom-right-radius: $defaultBorderRadius;

          &:before {
            content: '+';
          }
        }

        &.control-less {
          right: 1px;
          bottom: 1px;
          //border-top-left-radius: $defaultBorderRadius;
          border-bottom-right-radius: $defaultBorderRadius;

          &:before {
            content: '-';
          }
        }

        &.control-more:before,
        &.control-less:before {
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          height: 100%;
          width: 100%;
          -ms-flex-align: center;
          align-items: center;
          -ms-flex-pack: center;
          justify-content: center;
          display: -ms-flexbox;
          display: flex;
          font-weight: 300;
          font-size: 1rem;
        }

      }
    }

  }
</style>
