import { fetchAllTable } from "repositories/example-repository";

const fetchAll = ({ commit }) => fetchAllTable()
  .then(table => commit('SET_TABLE', table))

const state = {
  table: null
}

const actions = {
  fetchAllTable: fetchAll,
}

const getters = {
  table: state => state.table
}

const mutations = {
  SET_TABLE: (state, table) => state.table = table
}


export default {
  state,
  actions,
  getters,
  mutations
}
